import {
	mapState
} from 'vuex'

export default {
	computed: mapState(['forcedLogin', 'hasLogin', 'userName', 'userAvatar']),
	data() {
		return {
			parkingUserInfo: Object,
			modalName:null
		}
	},
	watch: {
		'parkingUserInfo.parkinglot_user_state': function(val, oldVal) {
			if (oldVal != null) {
				uni.showModal({
				    title: '提示',
				    content: '您已进入停车场',
				});
			}
			
		}
	},
	onShow() {
		let count = 0;
		if (this.hasLogin) {
			while (count < 1000) {
				setTimeout(() => {
					uni.request({
						url: 'http://118.31.77.203:8080/Entity/U21a840a21ebf11/PeterPark/Parkinglotuser/?Parkinglotuser.parkinglot_user_id=45', //仅为示例，并非真实接口地址。
						data: {},
						header: {
							'custom-header': 'hello' //自定义请求头信息
						},
						success: (res) => {
							this.parkingUserInfo = res.data.Parkinglotuser[0];
						}
					});
				}, count * 1000);
				count += 1;
			}

		}
	},
	onLoad() {
		if (!this.hasLogin) {
			uni.showModal({
				title: '未登录',
				content: '您未登录，需要登录后才能继续',
				/**
				 * 如果需要强制登录，不显示取消按钮
				 */
				showCancel: !this.forcedLogin,
				success: (res) => {
					if (res.confirm) {
						/**
						 * 如果需要强制登录，使用reLaunch方式
						 */
						if (this.forcedLogin) {
							uni.reLaunch({
								url: '../login/login'
							});
						} else {
							uni.navigateTo({
								url: '../login/login'
							});
						}
					}
				}
			});
		}
	},
	methods: {
		showModal(e) {
			this.modalName = e.currentTarget.dataset.target
		},
		hideModal(e) {
			this.modalName = null
		},
		tabSelect(e) {
			this.TabCur = e.currentTarget.dataset.id;
			this.scrollLeft = (e.currentTarget.dataset.id - 1) * 60
		}
	},
}
